import './VideoItem.css';
import React from 'react';

const VideoItem = ({ video, onVideoSelect }) => {

    return (
        <div className="video-hover video-item item" onClick={() => onVideoSelect(video)}>
            <img className="img ui image" alt={video.snippet.title} src={video.snippet.thumbnails.high.url} />
            <div className="content">
                <div className="header">
                    {video.snippet.title}
                </div>

            </div>
        </div>
    )
}

export default VideoItem;